import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Mais de 938 mil km so constitudos em Unidades de Conservao Ambiental em 2018</h2>

        <p className="simple-text">Institudas para fortalecer a proteo de reas de preservao, as unidades de conservao ambiental criadas pelo Governo do Brasil em 2018 representam o equivalente a quase duas vezes o territrio da Frana. Agora, o Pas passa a ter 333 reservas em todo o territrio nacional.</p>
        <p className="simple-text">Em abril, foram decretadas trs reservas extrativistas: Arapiranga Troma, Baa do Tubaro e Itapetiningano, todas no Maranho. So mais de 400 mil hectares preservados que vo beneficiar 13 mil famlias que vivem nesses locais de pesca artesanal. Alm das reservas, um parque nacional e uma rea de proteo ambiental em reas de caatinga tambm entraram para a lista de zonas preservadas.</p>
        <p className="simple-text">Em maro, foram outras quatro unidades marinhas delimitadas, as maiores da histria. Os arquiplagos So Pedro e So Paulo, em Pernambuco, e o de Trindade e Martim Vaz, em Vitria (ES), somam mais de 925 mil quilmetros quadrados em reas que sero em parte para uso sustentvel e em outra de proteo integral. Com isso, o Brasil passar de 1,5% de reas marinhas protegidas para 25%. </p>
        <p className="simple-text">Destaque ainda para o decreto que triplica a rea do Parque da Chapada dos Veadeiros, em Gois, e a ampliao de outras duas unidades de conservao: Estao Ecolgica do Taim, no Rio Grande do Sul, e a Reserva Biolgica Unio, no Rio de Janeiro.</p>
        <p className="smaller-title-text">Fonte: ICMBio e Ministrio do Meio Ambiente</p>
    </div>
)


