import React from 'react';
import HeaderContent from '../../components/HeaderContent';
import thumbnail from '../../assets/img/infos/[banner]-2_anos_materia-56-01.jpg';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Carto Reforma: Governo do Brasil investe R$ 685,5 para auxiliar brasileiros a renovar suas casas</h2>

        <p className="simple-text">Em 2018, o Carto Reforma ir beneficiar as famlias de todo o Pas com subsdio de at R$ 685,5 milhes.</p>
        <p className="eye-text">Com o incentivo, o Governo do Brasil proporciona melhores condies de moradia para as famlias de baixa renda e d mais dignidade aos brasileiros.</p>
        <p className="simple-text">Podero ser realizadas reformas, expanso ou concluso dos lares em bairros selecionados pelas prefeituras. O intuito  que sejam feitas melhorias necessrias para que a habitao tenha mais conforto e segurana, podendo incluir esgotamento sanitrio e banheiro exclusivo.</p>
        <p className="simple-text">As famlias cadastradas podero receber de R$ 2 mil a R$ 9 mil para compra de materiais de construo em lojas credenciadas pelo programa. Dessa forma, elas ficaro responsveis apenas pelos gastos com mo de obra e ferramentas.</p>
        <p className="smaller-title-text">Fonte: Ministrio das Cidades, Carto Reforma</p>
        <p className="infos">
            <img alt="" src={thumbnail}/>
        </p>
    </div>
)


