import React from 'react';
import HeaderContent from '../../components/HeaderContent';

import thumbnail from '../../assets/img/infos/[Banner]-2_anos_materia_20.jpg';

export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Agricultura familiar recebe crdito de R$ 30 bilhes para a safra 2017/2018</h2>

        <p className="simple-text">Na safra de 2017/2018, a agricultura familiar pode contar com um crdito de R$ 30 bilhes a uma taxa de juros mais baixa, entre 2,5% e 5,5%. </p>
        <p className="eye-text">Com os valores disponveis, os pequenos agricultores podem investir em irrigao, armazenagem e prticas sustentveis de manejo do solo e da gua, alm das tecnologias de energia renovvel.</p>
        <p className="simple-text">So R$ 403 milhes a mais em crdito em relao  ltima safra. Os recursos, do Programa Nacional de Fortalecimento da Agricultura Familiar (Pronaf), fortalecem a produo de alimentos de forma sustentvel. Anunciada pelo presidente da Repblica, Michel Temer, durante lanamento do plano Safra, apoia os pequenos produtores e ajuda a combater a pobreza, estimulando o emprego e o desenvolvimento de regies carentes.</p>
        <p className="smaller-title-text">Fonte: Secretaria Especial de Agricultura Familiar e do Desenvolvimento Agrrio</p>
        <p className="infos">
            <img alt="" src={thumbnail}/>
        </p>
    </div>
)


