import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Nmero de consultas nas UBS cresce 230% em 2017</h2>

        <p className="simple-text">A gesto mais eficiente e os investimentos constantes do Governo do Brasil motivaram um crescimento de 230% no nmero de consultas nas unidades bsicas de Sade (UBS) do Pas, na comparao entre 2016 e 2017.</p>
        <p className="eye-text">Foram 649 milhes de atendimentos no ltimo ano, contra 196,3 milhes em 2016.</p>
        <p className="simple-text">Atualmente, 70% da populao brasileira recebe cobertura da ateno bsica, com 41.668 UBS funcionando e atuao de 264.188 agentes comunitrios de sade e 41.991 equipes de sade da famlia.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Sade</p>
    </div>
)


