import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Governo prope salrio mnimo de R$ 1.002 para 2019</h2>
        <p className="simple-text">Pela primeira vez no Brasil depois do Plano Real, o valor do salrio mnimo ultrapassar R$ 1 mil. Na proposta de Lei de Diretrizes Oramentrias (LDO) enviada ao Congresso Nacional e assinada pelo presidente da Repblica, Michel Temer, est a sugesto de R$ 1.002, aumento de 5% em relao ao valor atual  R$ 954. </p>
        <p className="eye-text">A proposta foi possvel graas  poltica econmica implantada a partir de maio de 2016.</p>
        <p className="simple-text">O valor anunciado respeita a aplicao das regras atuais de correo, que define que o reajuste do salrio mnimo deve ser corrigido pela inflao do ano anterior, medida pelo ndice Nacional de Preos ao Consumidor (INPC), somada  variao do Produto Interno Bruto (PIB, soma dos bens e dos servios produzidos no Pas) dos dois anos anteriores.</p>
        <p className="smaller-title-text">Fonte: Ministrio do Planejamento</p>
    </div>
)


