import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Fies ter 350 mil vagas em 2018, sendo 100 mil a juro zero</h2>

        <p className="simple-text">O Novo Fundo de Financiamento Estudantil (Fies) conta, em 2018, com 310 mil vagas em instituies de ensino superior privadas em todo o Pas, sendo 100 mil a juro zero. Com o novo formato, as regras ficam mais simples, tornando o acesso dos estudantes de baixa renda ao ensino superior. </p>
        <p className="eye-text">Agora haver maior cobrana de qualidade dos cursos financiados, alm de transformar o perodo de carncia mais flexvel. As mudanas visam  sustentabilidade do programa a mdio e longo prazos.</p>
        <p className="simple-text">O Novo Fies vem com um fundo garantidor de adeso obrigatria pelas faculdades participantes do programa. Ele, que ser formado, em maior parcela, por aportes das instituies, com aportes da Unio, garante o crdito para os financiamentos contratados. A previso  que tenha caixa de R$ 3 bilhes.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Educao</p>
    </div>
)


