import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Nova grade curricular unifica ensino pblico e privado no Brasil</h2>
        <p className="simple-text">Pela primeira vez na histria, o Brasil possui uma Base Nacional Comum Curricular (BNCC), determinando o que deve ser ensinado nas escolas pblicas e privadas, unificando o ensino em todo o territrio nacional. Com a mudana, o Pas se alinha aos melhores sistemas educacionais do mundo.</p>
        <p className="eye-text">A etapa da base que diz respeito  educao infantil e ao ensino fundamental foi homologada em 2017 e j  uma norma para as redes e escolas de todo o Pas. J a parte do ensino mdio deve ser homologada at o fim de 2018, aps debate entre governo e sociedade.</p>
        <p className="simple-text">Visando ao aprimoramento do sistema, o Governo do Brasil tambm impulsiona o ensino em tempo integral. At 2020, sero investidos, via Poltica de Fomento  Escola em Tempo Integral, R$ 1,5 bilho na implantao do modelo.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Educao</p>
    </div>
)


