import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Governo refora operaes na faixa de fronteira</h2>

        <p className="simple-text">Os  quase 17 mil quilmetros de fronteira do Brasil tiveram aes de segurana reforada nos ltimos dois anos. Com decreto assinado em 2016 pelo presidente da Repblica, Michel Temer, as Foras Armadas passaram a ter atuao mais frequente em toda a divisa com os 10 pases sul-americanos. </p>
        <p className="eye-text">Somente em 2017, foram 88 operaes visando  segurana dos brasileiros.</p>
        <p className="simple-text">Ainda em 2017, a Fora Area Brasileira passou a realizar a Operao Ostium, de reforo na vigilncia do espao areo sobre a regio de fronteira do Brasil, em combate ao narcotrfico e a trfegos ilcitos. </p>
        <p className="simple-text">Voltada ao combate de crimes transfronteirios e ambientais, a operao gata foi reformulada com decreto presidencial. Agora, ocorrendo de maneira fracionada e aleatria, ganha carter mais imprevisvel para o crime organizado.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Defesa, Fora Area Brasileira </p>
    </div>
)


