import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Governo investe R$ 1 bilho na formao de professores</h2>
        <p className="simple-text">Ao investir R$ 1 bilho na Poltica Nacional de Formao de Professores em 2018, o Governo do Brasil refora o compromisso com a formao profissional dos docentes brasileiros. Ao total, so 190 mil vagas criadas, sendo 45 mil no Programa Institucional de Bolsas de Iniciao  Docncia (Pibid), 45 mil no Programa de Residncia Pedaggica e mais 100 mil na Universidade Aberta do Brasil (UAB).</p>
        <p className="simple-text">A formao dos professores faz parte da poltica do governo de investimentos constantes no sistema educacional do Brasil. Ter docentes mais qualificados significa um ensino de mais qualidade para crianas e jovens e bases mais prsperas para a economia do Pas continuar crescendo.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Educao</p>
    </div>
)


