import React from 'react';
import HeaderContent from '../../components/HeaderContent';
import thumbnail from '../../assets/img/infos/[Info]-2_anos_materia-2.png';
export default () => (
    <div>
        <HeaderContent></HeaderContent>

        <h2 className="materia-title">PIB de 1% e tomada do crescimento aps a maior recesso em 20 anos</h2>

        <p className="simple-text">Puxado principalmente pela agropecuria, que cresceu 13% em 2017, o Produto Interno Bruto (PIB) do Brasil cresceu 1% em 2017, depois de ficar negativo por dois anos consecutivos. A recuperao foi possvel graas s medidas apresentadas pelo governo, que permitiram a reduo dos juros e controle da inflao.</p>
        <p className="eye-text">Junto  agropecuria, bons resultados foram observados no setor de servios (+0,3%) e na taxa de investimentos, que ficou em 15,6% em 2017. J o consumo das famlias cresceu 1%. No total, o PIB do Brasil somou R$ 6,6 trilhes.</p>
        <p className="simple-text">O Produto Interno Bruto (PIB)  a soma de todos os bens e servios produzidos no Pas. Esse nmero  usado para mostrar o quanto se produziu e o desempenho das atividades econmicas durante um determinado perodo de tempo.</p>
        <p className="smaller-title-text">Fonte: IBGE</p>
        <p className="infos">
            <img alt="" src={thumbnail}/>
        </p>
    </div>
)


