import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Saques das contas inativas do FGTS injetam R$ 44 bilhes na economia</h2>

        <p className="simple-text">Medida indita apresentada pelo Governo do Brasil, os saques das contas inativas do Fundo de Garantia do Tempo de Servio (FGTS) totalizaram R$ 44 bilhes no primeiro semestre de 2017. No total, foram 25,9 milhes de brasileiros beneficiados pela iniciativa, que ajudou a movimentar a economia do Pas no ano passado.</p>
        <p className="simple-text">Com o recurso extra sacado, os brasileiros puderam quitar dvidas, fazer novos investimentos e aumentar o seu consumo, impactando diretamente no comrcio e no setor de servios, estimulando a economia. </p>
        <p className="eye-text">A Caixa tambm verificou, durante os meses de pagamento, um aumento nas contrataes de financiamento habitacional na ordem de 27%. </p>
        <p className="simple-text">A medida beneficiou trabalhadores que pediram demisso at 31 de dezembro de 2015 ou que no conseguiram sacar os recursos no caso de demisso por justa causa.</p>
        <p className="smaller-title-text">Fonte: Caixa Econmica Federal e Ministrio do Trabalho</p>
    </div>
)


