import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Governo lana documentos digitais que simplificam a vida do brasileiro</h2>
        <p className="simple-text">O Governo do Brasil d passos importantes rumo  identificao digital do cidado brasileiro e  reduo da burocracia na administrao. As inovaes possibilitam ainda mais agilidade nos processos e mais transparncia para a populao.</p>
        <p className="simple-text">Uma das solues propostas, o Documento Nacional de Identificao (DNI) rene diferentes registros civis em um nico documento, com validade em todo territrio nacional. A nova identificao dispensa a apresentao de outros papis como CPF, certido de nascimento, casamento ou ttulo de eleitor.</p>
        <p className="eye-text">Pelo aplicativo, o empregado pode verificar informaes pessoais, de contratos de trabalho e tambm solicitar a primeira e a segunda via da carteira de trabalho fsica.</p>
        <p className="simple-text">Tambm realidade, a Carteira de Trabalho Digital permite que os trabalhadores tenham, em mos, sempre que precisarem, todas as informaes que constam no documento fsico. Apesar de no substitui-lo, o documento facilita as consultas.</p>
        <p className="smaller-title-text">Fonte: Ministrio do Planejamento e Ministrio do Trabalho</p>
    </div>
)


