import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Governo oferece melhor tratamento do mundo para HIV/Aids</h2>
        <p className="simple-text">Ao incorporar o antirretroviral dolutegravir ao Sistema nico de Sade (SUS), o Governo do Brasil passou a oferecer o melhor tratamento do mundo para HIV/Aids. Inicialmente ofertado a 100 mil pacientes, o tratamento  gradativamente ampliado a todos os portadores do vrus que so acompanhados pelo SUS at o fim de 2018.</p>
        <p className="eye-text">A oferta do tratamento ocorre aps a reduo negociada de 70% no valor do medicamento. Dessa forma, a incorporao pode ser feita sem alterar o oramento da Sade para a aquisio de antirretrovirais.</p>
        <p className="simple-text">Considerado um dos melhores medicamentos do mundo para pessoas que vivem com HIV e Aids, o dolutegravir apresenta uma srie de vantagens como alta potncia; nvel muito baixo de eventos adversos; comodidade para o paciente com uma dose ao dia; e tratamento eficaz por mais tempo e menor resistncia.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Sade</p>
    </div>
)


