import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Em dois anos, Governo amplia em 95% investimentos em aes e servios em Sade</h2>
        <p className="simple-text">Os investimentos foram garantidos com adicional de recursos de R$ 6,6 bilhes, sendo possvel atender a projetos e possibilitar a aquisio de equipamentos em sade, construo de unidades bsicas de Sade (UBS) e unidades de Pronto Atendimento (UPAs), reforma e ampliao de obras e servios de sade, entre outros.</p>
        <p className="eye-text">Em dois anos, o Governo do Brasil aumentou em 95% a execuo oramentria da Sade e garantiu a aplicao de recursos, beneficiando milhes de brasileiros com mais servios e aes em sade.</p>
        <p className="simple-text">Para 2018, est previsto para o setor o valor recorde de R$ 131 bilhes no oramento da Unio. Sero R$ 119,3 bilhes para aes de servios pblicos de sade, R$ 1,1 bilho acima do mnimo constitucional.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Sade</p>
    </div>
)


