import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Repasses na Sade ganham reforo  ateno bsica com atualizao da base populacional</h2>
        <p className="simple-text">A populao brasileira ser beneficiada com reforo anual de R$ 306 milhes no Piso da Ateno Bsica, principal porta de entrada do Sistema nico de Sade (SUS). Em 2018, o valor total do piso repassado ser de R$ 5,1 bilhes. O adicional ocorre pela atualizao da base populacional dos municpios, que serve de referncia para o clculo e que passa a ter como referncia a estimativa de 2016 do IBGE. At ento, a orientao utilizada era a de 2012.</p>
        <p className="eye-text">Prioridade do governo, a ateno bsica tem recebido recursos crescentes para melhorar a sade da populao que depende da rede pblica. Com o adicional, ser possvel qualificar os servios da especialidade, nos quais 80% dos problemas de sade podem ser resolvidos,.</p>
        <p className="simple-text">Nos ltimos dois anos, foram investidos mais de R$ 1 bilho para custear e reforar os servios e equipes. O valor garante ampliao nos atendimentos e contempla mais de 20 milhes de pessoas que vivem em 2.386 municpios brasileiros.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Saude</p>
    </div>
)


