import React from 'react';
import HeaderContent from '../../components/HeaderContent';

import thumbnail from '../../assets/img/infos/[info] 2_anos_materia 17.jpg';

export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Aps 20 anos de disputas, bancos e poupadores fecham acordo de R$ 12 bilhes</h2>

        <p className="simple-text">Com mediao da Advocacia-Geral da Unio (AGU), bancos e poupadores afetados por planos econmicos dos anos 1980 e 1990 esto mais prximos de encerrar as divergncias. Acordo homologado pelo Supremo Tribunal Federal em fevereiro prev o fim de cerca de 1 milho de aes judiciais e deve injetar R$ 12 bilhes na economia.</p>
        <p className="eye-text">Para receber os valores, os clientes que tm processos precisaro abrir mo dessas disputas e aderir a um acordo que estar disponvel em uma plataforma eletrnica ainda no primeiro semestre de 2018.</p>
        <p className="simple-text">A deciso termina com um impasse importante para o sistema financeiro e define regras claras para os pagamentos. Tambm contribui com a retomada do crescimento econmico por permitir que recursos que estavam parados nos bancos voltem a circular, gerando renda e consumo.</p>
        <p className="smaller-title-text">Fonte: Banco Central, Advocacia-Geral daUnio, Supremo Tribunal Federal</p>
        <p className="infos">
            <img alt="" src={thumbnail}/>
        </p>
    </div>
)


