import React from 'react';
import HeaderContent from '../../components/HeaderContent';
import thumbnail from '../../assets/img/infos/[Info]-2_anos_materia-8.png';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Saques do PIS/Pasep beneficiam 11,2 milhes de trabalhadores; impacto chega a R$ 21,4 bi</h2>

        <p className="simple-text">Ao reduzir a idade mnima para o saque das contas dos programas de Integrao Social (PIS) e de Formao do Patrimnio do Servidor Pblico (Pasep), o Governo do Brasil apresenta mais uma medida para estimular a economia do Pas. So 11,2 milhes de trabalhadores, com mais de 60 anos de idade, com direito  retirada dos valores que pode injetar at R$ 21,4 bilhes na economia.</p>
        <p className="simple-text">Em agosto de 2017, a idade de saque de 70 anos foi alterada para 65 anos para homens e 62 anos para mulheres. Com a inteno de manter o estmulo e ampliar o nmero de beneficirios, o governo editou em dezembro uma nova medida provisria  dessa vez, reduzindo a idade para 60 anos para todos os beneficirios. Aposentados, herdeiros, quem foi transferido para a reserva remunerada ou reforma ou casos de invalidez tambm esto contemplados.</p>
        <p className="simple-text">Os fundos PIS/Pasep vigoraram entre 1971 e 1988, ano em que foram incorporados ao Fundo de Apoio ao trabalhador (FAT). Com a mudana, muitos trabalhadores que tinham o direito no sacaram a totalidade de seus saldos.</p>
        <p className="eye-text">Com essas medidas, o Governo do Brasil possibilita que o dinheiro retorne s mos dos brasileiros e amplia o poder de compra da populao.</p>
        <p className="smaller-title-text">Fonte: Caixa Econmica Federal e Ministrio do Planejamento</p>
        <p className="infos">
            <img alt="" src={thumbnail}/>
        </p>
    </div>
)


