import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Plano Agro+ moderniza e reduz a burocracia do agronegcio brasileiro</h2>
        <p className="simple-text">Criado pelo Governo do Brasil em 2017, o Plano Agro+ j simplificou normas e modernizou operaes em 830 aes. O intuito das mudanas  melhorar o fluxo de informaes, reduzir a burocracia, simplificar as normas e tornar mais eficientes as operaes do agronegcio brasileiro.</p>
        <p className="eye-text">Um dos objetivos do plano  aumentar de 7% para 10% a participao do Brasil no comrcio agrcola mundial. A ao corrobora com o saldo positivo da balana comercial brasileira, gerando mais riqueza para todo o Pas.</p>
        <p className="simple-text">Ponto de destaque no plano, a atualizao de normas e procedimentos era uma reivindicao antiga do setor produtivo, que contribuiu com sugestes na execuo do programa. A estimativa  que, com o Agro+, o setor privado e o governo tenham ganho de eficincia da ordem de R$ 1 bilho ao ano, o que representa 0,2% do faturamento anual do agronegcio.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Agriculta, Pecuria e Abastecimento  </p>
    </div>
)


