import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Com lei de governana e melhor gesto, estatais fecham 2017 com lucro de R$ 28,4 bilhes </h2>

        <p className="simple-text">As empresas estatais federais evoluram de um prejuzo de R$ 32 bilhes em 2015 para um lucro de R$ 28,4 bilhes em 2017, representando uma variao positiva de R$ 60,4 bilhes. O desempenho  resultado direto de investimento em gesto e da Lei da Responsabilidade das Estatais, sancionada pelo presidente da Repblica, Michel Temer, em 2016.</p>
        <p className="eye-text">Com a sano da lei, transparncia, regras mais rgidas para compras, licitaes e nomeaes e boas prticas de governana corporativas so requisitos exigidos para a gesto das estatais. Em 2016, os primeiros resultados apareceram: saldo positivo de R$ 9 bilhes. </p>
        <p className="simple-text">Esse desempenho expressivo  o acumulado apenas das maiores empresas: Banco do Brasil, Banco Nacional de Desenvolvimento Econmico e Social (BNDES), Caixa Econmica Federal, Eletrobras e Petrobras.</p>
        <p className="smaller-title-text">Fonte: Ministrio do Planejamento, Portal Legislao</p>
    </div>
)


