import React from 'react';
import HeaderContent from '../../components/HeaderContent';
import thumbnail from '../../assets/img/infos/[banner]-2_anos_materia-57-01.jpg';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Em 20 meses, 793 mil novas unidades do Minha Casa Minha Vida so contratadas</h2>

        <p className="simple-text">Com uma mdia de mais de 39 mil residncias novas por ms, o Governo do Brasil ampliou o programa Minha Casa Minha Vida, retomando sonhos e dando impulso  construo civil. No total, foram 793 mil unidades contratadas, entre maio de 2016 e janeiro de 2018, ao valor de R$ 101 bilhes.</p>
        <p className="eye-text">Em 2018, no ser diferente. Com um investimento total previsto de R$ 72,7 bilhes, mais 650 mil unidades habitacionais sero construdas. </p>
        <p className="simple-text">Parte da verba vir do Oramento da Unio (R$ 9,7 bilhes), e o restante (R$ 63 bilhes), do Fundo de Garantia do Tempo de Servio (FGTS).</p>
        <p className="smaller-title-text">Fonte: Ministrio das Cidades</p>
        <p className="infos">
            <img alt="" src={thumbnail}/>
        </p>
    </div>
)


