import React from 'react';
import HeaderContent from '../../components/HeaderContent';
import thumbnail from '../../assets/img/infos/[Info]-2_anos_materia-9.png';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Saldo da balana comercial em 2017 bate recorde: saldo de US$ 67 bilhes</h2>

        <p className="simple-text">Em 2017, as exportaes brasileiras superaram as importaes em US$ 67 bilhes, um saldo histrico para o Pas. Os dados tambm revelam o primeiro crescimento das exportaes do Brasil em cinco anos e das importaes em trs.</p>
        <p className="eye-text">Se comparado com o ano anterior, o resultado  ainda mais expressivo: 40,5% superior ao alcanado em 2016 (US$ 47,683 bilhes).</p>
        <p className="simple-text">Produtos como minrio de ferro, soja e petrleo bruto foram destaques pelo crescimento no volume ou valor exportado. Em 2018, dados do primeiro trimestre mostram continuidade no crescimento, com saldo comercial positivo de US$ 13,9 bilhes. A expectativa do governo  que este seja o segundo ano consecutivo de crescimento do comrcio.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Industria, Comrcio Exterior e Servios</p>
        <p className="infos">
            <img alt="" src={thumbnail}/>
        </p>
    </div>
)


