import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Meio ambiente brasileiro pode ter at R$ 4,6 bi em projetos de recuperao</h2>

        <p className="simple-text">Duas iniciativas inditas do Governo do Brasil vo permitir o uso de multas ambientais em projetos de recuperao de reas degradadas. No total, cerca de R$ 4,6 bilhes podero ser destinados a aes em reas desmatadas e tambm nas bacias dos rios So Francisco e Parnaba, fundamentais para o abastecimento de gua no semirido nordestino.</p>
        <p className="simple-text">Pelos decretos assinados pelo presidente da Repblica, Michel Temer, as multas podem ser convertidas em prestao de servios de preservao, melhoria e recuperao do meio ambiente. Independentemente da converso, o reparo da rea danificada continua sendo uma obrigao do infrator.</p>
        <p className="smaller-title-text">Fonte: IBAMA</p>
    </div>
)


