import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Governo adquire repelentes para gestantes de baixa renda</h2>
        <p className="simple-text">Mulheres grvidas inscritas no programa Bolsa Famlia podem receber de maneira gratuita repelentes para se proteger contra o Aedes aegypti, transmissor da dengue, zika e chikungunya. O benefcio est disponvel desde 2017, quando Governo do Brasil investiu R$ 82 milhes na aquisio do produto, visando beneficiar 500 mil mulheres.</p>
        <p className="eye-text">O governo recomenda o uso de repelentes para reforar a proteo contra o mosquito Aedes aegypti, em especial s gestantes, pela associao do zika vrus com a microcefalia em bebs.</p>
        <p className="simple-text">Em 2008, com intuito de estender o benefcio para outras pessoas em situao de vulnerabilidade, os gestores da sade decidiram ampliar o pblico-alvo da ao. A escolha dos novos grupos prioritrios ser feita pelos estados, em funo das realidades locais.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Sade</p>
    </div>
)


