import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Proposta indita estabelece teto para os gastos pblicos</h2>

        <p className="simple-text">Ao essencial para o ajuste das contas pblicas, a emenda constitucional do teto de gastos, elaborada pelo Governo do Brasil e promulgada pelo Congresso Nacional em dezembro de 2016, tornou regra um conceito que deveria ser bsico para todos os governantes: no se pode gastar mais do que se arrecada.</p>
        <p className="eye-text">Marco histrico para a economia do Pas, a proposta limita o crescimento das despesas do governo para os prximos 20 exerccios financeiros.</p>
        <p className="simple-text">Dessa forma, as despesas primrias da Unio ficaram restritas ao que foi gasto no ano anterior, corrigidas pela inflao.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Fazenda</p>
    </div>
)


