import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Novo formato da carteira de habilitao oferece mais segurana aos motoristas</h2>
        <p className="simple-text">A Carteira Nacional de Habilitao (CNH) ter um novo formato a partir de janeiro de 2019. O documento, atualmente em papel, ser semelhante a um carto de crdito, de plstico e com chip, para aumentar a segurana e evitar fraudes.</p>
        <p className="eye-text">Alm da inovao no documento fsico, os motoristas tambm podem contar com a verso eletrnica da CNH. O documento criptografado j est em vigor e fica acessvel nos telefones celulares via um aplicativo seguro. A verso eletrnica possui a mesma validade do documento em papel.</p>
        <p className="simple-text">Alm da resistncia e da alta durabilidade, a nova CNH considera a possibilidade de insero de dados e informaes relativas aos condutores nos chips, bem como facilita acesso a certificados digitais. Ela amplia as possibilidades de uso dos documentos, a consulta e a verificao de inmeros dados</p>
        <p className="smaller-title-text">Fonte: Ministrio das Cidades</p>
    </div>
)


