import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Em dois anos, governo renova 65% da frota do SAMU</h2>

        <p className="simple-text">Com a entrega de 2.173 novas ambulncias, o Governo do Brasil ter renovado em 65% a frota do Servio de Atendimento Mvel de Urgncia (SAMU) at o fim de 2018. </p>
        <p className="eye-text">A medida complementa o repasse de outros 1.338 veculos realizado em 2017, alm das 196 concedidas via emenda parlamentar, marcando um dos ndices de renovao mais altos da histria do Ministrio da Sade.</p>
        <p className="simple-text">Alm das ambulncias, foi destinado mais R$ 1 bilho para a compra de 1 mil vans para transporte sanitrio eletivo e 6,5 mil ambulncias brancas  usadas no transporte de pacientes. Desde 2004 que no havia compra de veculos para transporte sanitrio.</p>
        <p className="simple-text">Atualmente o SAMU est presente em 3.533 municpios, com 3.307 ambulncias, sendo 2.702 unidades de suporte bsico e 605 de suporte avanado. Ao todo, 170,6 milhes de pessoas (82,2% da populao) contam com a cobertura do servio.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Sade</p>
    </div>
)


