import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Planos de sade tm cobertura mnima de exames, terapias e procedimentos ampliada</h2>

        <p className="simple-text">Entrou em vigor em janeiro a nova cobertura mnima dos planos de sade estipulada pela Agncia Nacional de Sade Suplementar (ANS).</p>
        <p className="eye-text">Foram includos 18 novos procedimentos, entre cirurgias, exames e terapias.</p>
        <p className="simple-text">Sete procedimentos, incluindo remdios orais para o tratamento de cnceres de pulmo, prstata, endcrinos, mielofibrose e leucemia, tambm entraram na lista. Outro avano: pela primeira vez, os planos devem custear medicamento para tratar esclerose mltipla.</p>
        <p className="simple-text">Para garantir a qualidade no atendimento e melhor acesso ao tratamento dos beneficirios dos planos de sade, o rol de procedimentos  atualizado a cada dois anos segundo critrios alinhados com a Poltica Nacional de Sade. </p>
        <p className="smaller-title-text">Fonte: Agncia Nacional de Sade Suplementar</p>
    </div>
)


