import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Brasil bate recorde na emisso de ttulos de propriedade em 2017</h2>

        <p className="simple-text">Com o objetivo de garantir mais segurana jurdica, bem-estar e dignidade para os brasileiros, o Governo do Brasil concedeu o ttulo de propriedade a 248 mil moradores de ocupaes irregulares em 2017. Destes, 42 mil so ttulos de reas urbanas ocupadas irregularmente e comprovadamente existentes at 22 de dezembro de 2016.</p>
        <p className="eye-text">A medida beneficia moradores de zonas urbanas e rurais e foi agilizada por lei sancionada em julho de 2017 pelo presidente da Repblica, Michel Temer.</p>
        <p className="simple-text">O novo marco legal visa  regularizao dos ncleos urbanos informais, contribuindo para desburocratizar e simplificar todo o processo, ampliando as possibilidades de acesso  terra urbanizada pela populao.</p>
        <p className="smaller-title-text">Fonte: Ministrio das Cidades</p>
    </div>
)


