import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Mesmo com Operao Carne Fraca, Brasil aumenta em 9% a exportao de protena animal</h2>
        <p className="simple-text">No ano da Operao Carne Fraca, o Governo do Brasil venceu a crise e garantiu aumento nas exportaes de protena animal. O trabalho para assegurar e expandir mercados e a qualidade da produo nacional resultou em exportaes de US$ 15,5 bilhes em 2017, aumento de 9% sobre os US$ 14,2 bilhes de 2016.</p>
        <p className="simple-text">Principal produto do setor, a carne de frango representou quase metade das exportaes (46,1%). Foram vendidos para o exterior US$ 7,14 bilhes do produto, 5,5% acima do registrado no ano anterior. As vendas de carne suna apresentaram recorde histrico, somando US$ 1,61 bilho, ou seja, 9,7% superiores a 2016.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Agricultura</p>
    </div>
)


