import React from 'react';
import HeaderContent from '../../components/HeaderContent';
import thumbnail from '../../assets/img/infos/[Info]-2_anos_materia-4.png';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Taxa bsica de juros  reduzida a 6,5%, a menor da histria</h2>

        <p className="simple-text">A taxa bsica de juros da economia, a Selic, atingiu o patamar de 6,5% em maro, o menor percentual da histria. Desde outubro de 2016, a reduo do ndice vem se repetindo regularmente. Usada como referncia pelo sistema financeiro, uma taxa menor significa mais empregos, maior crescimento para o Pas e mais acesso ao crdito.</p>
        <p className="simple-text">Isso s foi possvel depois de medidas adotadas pelo Governo do Brasil que criaram condies para taxas menores, como foi o caso do teto dos gastos, ajuste fiscal e outras aes coordenadas.</p>
        <p className="eye-text">Essas medidas contriburam com o resultado e geraram mais confiana. Aliado a isso, uma inflao menor deu o espao necessrio para o Banco Central aplicar os cortes.</p>
        <p className="smaller-title-text">Fonte: IBGE</p>
        <p className="infos">
            <img alt="" src={thumbnail}/>
        </p>
    </div>
)


