import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Com mudanas no Farmcia Popular, Sade amplia em R$ 80 milhes por ano recurso para medicamentos</h2>

        <p className="simple-text">As alteraes no custeio da rede prpria do programa Farmcia Popular permitiram ao governo direcionar a integralidade do oramento anual de R$ 100 milhes para a aquisio de medicamentos. Antes, 80% da verba era destinada ao pagamento de aluguis e outros custos administrativos.</p>
        <p className="eye-text">Com a mudana, a oferta efetiva de insumos farmacuticos nas unidades de sade de todo o Pas para o tratamento de doenas como diabetes, hipertenso e asma, por exemplo,  ampliada.</p>
        <p className="simple-text">Diretamente, ela possibilita a estados e municpios obter um incremento de 10% na compra de medicamentos da ateno bsica do Sistema nico de Sade.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Sade</p>
    </div>
)


