import React from 'react';
import HeaderContent from '../../components/HeaderContent';
import thumbnail from '../../assets/img/infos/[banner]-2_anos_materia-37-01.jpg';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Com avio de prontido, FAB assegura transporte de 476 rgos para transplante</h2>

        <p className="simple-text">A assinatura de um decreto pelo presidente da Repblica, Michel Temer, em junho de 2016 significou a chance de uma nova vida para centenas de pessoas aps a Fora Area Brasileira deixar uma aeronave de prontido para o transporte de rgos para transplantes. Nestes quase dois anos, foram 476 rgos em suas aeronaves.</p>
        <p className="eye-text">Somente nos primeiros meses de 2018, foram 29 fgados, 25 coraes, cinco pulmes, trs rins e um pncreas que chegaram a tempo para operaes vitais.</p>
        <p className="simple-text">A Fora Area  acionada quando no h voos comerciais para atender a uma emergncia e garante, assim, que a doao chegue a tempo ao paciente que est  espera. </p>
        <p className="smaller-title-text">Fonte: Fora Area Brasileira, Ministrio da Sade e Planalto</p>
        <p className="infos">
            <img alt="" src={thumbnail}/>
        </p>
    </div>
)


