import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Interveno federal contribui para reduo de ndices de criminalidade no Rio de Janeiro</h2>

        <p className="simple-text">Aps a interveno federal no Rio de Janeiro e a criao do Ministrio Extraordinrio da Segurana Pblica em fevereiro, os ndices de criminalidade na capital carioca j apontam melhoras. </p>
        <p className="eye-text">Medidas adotadas na cidade so de longo prazo, mas resultados positivos so constatados desde o primeiro ms.</p>
        <p className="simple-text">Em maro, ocorrncias como roubo a transeunte tiveram queda de 55% em relao ao mesmo perodo de 2017, passando de 559 para 251 casos. Crimes como roubo de carga e roubo a turista tambm seguiram a tendncia de diminuio. De janeiro a maro, o roubo de veculos caiu 18% no centro do estado do Rio de Janeiro e mais de 30% na capital e na Baixada Fluminense.</p>
        <p className="simple-text">Afim de custear as aes, medida provisria assinada pelo presidente da Repblica, Michel Temer, em maro garante oramento extra de R$ 1,2 bilho para as operaes da interveno federal na segurana pblica do Rio de Janeiro.</p>
        <p className="smaller-title-text">Fonte: Planalto, Ministrio da Defesa</p>
    </div>
)


