import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Governo investe em teste rpido para diagnstico de zika e garante assistncia a gestantes e crianas</h2>
        <p className="simple-text">Para dar mais agilidade ao diagnstico e tranquilizar gestantes e crianas, o Governo do Brasil investiu R$ 119 milhes na compra de 3,5 milhes de exames com capacidade de detectar o vrus zika em 20 minutos. A medida possibilitou um controle eficaz da contaminao e a assistncia imediata a crianas e gestantes.</p>
        <p className="eye-text">Outros dois milhes de testes rpidos para dengue e um milho para chikungunya tambm foram adquiridos pelo governo para abastecer estados e municpios. No trs casos, o material permite obter o resultado de maneira eficaz, mesmo que a contaminao tenha ocorrido anteriormente. Todos os insumos entraram na tabela do Sistema nico de Sade (SUS).</p>
        <p className="simple-text">Com medidas de preveno e de mobilizao contra o mosquito Aedes aegypti, transmissor da dengue, zika e chikungunya, foi possvel reduzir drasticamente a incidncia das trs doenas. Em 2018, houve reduo de 92% nos casos de zika, 83% nos de dengue e 32% nos de chikungunya em relao a 2017, ano que j apresentava nmeros positivos em relao ao anterior.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Sade</p>
    </div>
)


