import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Integrao do rio So Francisco j traz melhorias e gera milhares de empregos</h2>

        <p className="simple-text">Com a retomada da obra de integrao do rio So Francisco, as guas comearam a fluir pelos canais concludos em 33 cidades da Paraba e Pernambuco em maro do ano passado. Desde ento, um milho de residncias passaram a ser abastecidas. Moradores usam o recurso para consumo, plantio e criao de animais, recebendo mais qualidade de vida e gerando recursos econmicos.</p>
        <p className="eye-text">Em 2018, j so 297 quilmetros de canais e adutoras em operao, sendo 217 quilmetros no Eixo Leste. Com a empreitada que segue, 5,6 mil moradores da regio esto contratados na construo dos canais, melhorando a economia. </p>
        <p className="simple-text">A gerao de empregos tambm aconteceu com a inaugurao em maro dos 42 km de canal do Eixo Norte do Projeto Pblico de Irrigao Baixio de Irec, na Bahia. A irrigao das terras cria 25 mil empregos diretos e indiretos e favorece lavouras de algodo, cana-de-acar, frutas e legumes, beneficiando inmeras famlias. </p>
        <p className="simple-text">Parte do programa Agora,  Avanar, a obra est 96,5% concluda. Quando estiver pronta, vai abastecer 23 audes nordestinos, cujas capacidades estavam comprometidas pela seca prolongada. A expectativa  que at 12 milhes de pessoas sejam beneficiadas. O Governo do Brasil investiu R$ 550 milhes no projeto.</p>
        <p className="smaller-title-text">Fonte Planalto e Codevasf</p>
    </div>
)


