import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Governo habilita 206 UPAs 24 horas, ampliando o atendimento  popula</h2>
        <p className="simple-text">Em dois anos, 206 unidades de pronto atendimento (UPAs) foram habilitadas e qualificadas pelo Governo do Brasil, garantindo o atendimento de mais de 20 milhes de brasileiros. Todas as solicitaes de custeio foram atendidas, permitindo que estados e municpios tenham suporte financeiro da Unio.</p>
        <p className="eye-text">Atualmente, 572 UPAs 24h atendem os brasileiros em todo o territrio nacional. Outras 218 esto em obras, 10 em ao preparatria e outras 155 se encontram concludas, em fase de entrada em funcionamento.</p>
        <p className="simple-text">Ainda em 2007, o governo tambm realizou mudanas no modelo assistencial e de funcionamento das estruturas. Passando de trs para oito modalidades de habilitao, a gesto ficou mais flexvel, evitando que unidades prontas para funcionamento continuassem fechadas por motivos administrativos.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Sade</p>
    </div>
)


