import React from 'react';
import HeaderContent from '../../components/HeaderContent';
import thumbnail from '../../assets/img/infos/[info]-2_anos_materia-38-01.jpg';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Brasil tem reduo de 92% nos casos de zika e 83% nos casos de dengue em 2017</h2>

        <p className="simple-text">A grande mobilizao do Governo do Brasil, com a participao de estados e municpios, teve resultados positivos: as trs doenas transmitidas pelo Aedes agypti, dengue, zika e chicungunya, tiveram reduo significativa em 2017.</p>
        <p className="eye-text">No caso da zika, a reduo chegou a 92%, e a da dengue foi de 83%. Quanto  chikungunya, tambm houve baixa de 33% em relao a 2016.</p>
        <p className="simple-text">Medidas de combate ao mosquito e as campanhas de preveno surtiram efeito e ajudaram na conscientizao de que todos devem agir para evitar os focos do mosquito Aedes aegypti.</p>
        <p className="simple-text">Familiares dos bebs acometidos pela sndrome congnita do zika vrus e de microcefalia tambm puderam contar com mais Centros Especializados em Reabilitao (CERs) para a realizao do tratamento. Entre maio de 2016 e fevereiro de 2018, 67 centros foram habilitados, representando um investimento de R$ 132 milhes por ano para custeio.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Sade</p>
        <p className="infos">
            <img alt="" src={thumbnail}/>
        </p>
    </div>
)


