import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Polcia Federal apreende 44,7 toneladas de cocana e 313 de maconha e bate recorde histrico</h2>

        <p className="simple-text">A Polcia Federal bateu seu recorde histrico de apreenses de cocana e maconha em 2017. Com operaes das Delegacias de Represso a Entorpecentes e do Grupo Especial de Investigaes Sensveis (Gise), a PF tirou de circulao 44,7 toneladas de cocana e 313 toneladas de maconha.</p>
        <p className="eye-text">O valor de bens apreendidos tambm  recorde: R$ 591,4 milhes. Antes, a maior cifra era de 2014, com R$ 323 milhes. A polcia rastreou, e a Justia apreendeu mais de R$ 665 milhes em bens e em dinheiro do trfico, quase o triplo do que em 2016.</p>
        <p className="simple-text">Nas rodovias federais, entre julho de 2017 e abril de 2018, foram 14 mil pessoas presas, 205 toneladas de maconha e seis toneladas de cocana e crack apreendidas, alm de 1 mil armas de fogo e 192 mil munies. Tambm foram recuperados 3.200 mil carros roubados/furtados/usados em outros crimes.</p>
        <p className="smaller-title-text">Fonte: Polcia Federal</p>
    </div>
)


