import React from 'react';
import HeaderContent from '../../components/HeaderContent';
import thumbnail from '../../assets/img/infos/[Banner]-2_anos_materia-86-01.jpg';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Programa ID Jovem conta com 500 mil cadastros e amplia acesso  cultura</h2>

        <p className="simple-text">Criado em 2016, o Programa Identidade Jovem (ID Jovem) j conta com mais de 500 mil cadastros efetivos, levando mais cultura aos brasileiros de 15 a 29 anos inscritos no Cadastro nico. </p>
        <p className="eye-text">Com o benefcio, o Governo do Brasil assegura o acesso de milhes de jovens de baixa renda  cultura e  mobilidade urbana.</p>
        <p className="simple-text">Em 2018, a Secretaria Nacional da Juventude manter um calendrio de caravanas para divulgar o programa nos municpios e fortalecer a poltica de incluso onde j houve atuao, alm de uma ampla campanha de fiscalizao do programa.</p>
        <p className="simple-text">O ID Jovem permite que jovens de baixa renda de at 29 anos tenham direito  meia-entrada em eventos culturais e gratuidade ou desconto no sistema de transporte coletivo interestadual. O cadastro pode ser feito on-line pelo site da Caixa ou por meio de aplicativo para smartphones.</p>
        <p className="smaller-title-text">Fonte: Secretaria Nacional da Juventude</p>
        <p className="infos">
            <img alt="" src={thumbnail}/>
        </p>
    </div>
)


