import React from 'react';
import HeaderContent from '../../components/HeaderContent';
import thumbnail from '../../assets/img/infos/[Info]-2_anos_materia-01.png';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Em 2017, Brasil tem menor inflao em duas dcadas</h2>

        <p className="simple-text">Menor ndice registrado em duas dcadas, a inflao de 2,97% de dezembro passado  resultado direto das medidas econmicas apresentadas pelo Governo do Brasil. Mais qualidade de vida para a  populao, gerao de empregos, aumento do poder de compra dos brasileiros e mais rendimento na poupana.</p>
        <p className="eye-text">Uma economia mais forte beneficia, em especial, quem ganha menos.<br /> Em 2017, a inflao tambm foi a menor desde 1998 para as famlias de baixa renda.</p>
        <p className="simple-text">Pela primeira vez, o resultado ficou abaixo do piso da meta estabelecida pelo Governo, que era de 3%. Alm das medidas do governo, a supersafra de gros, que gerou reduo no preo dos alimentos, tambm contribuiu para o ndice histrico.</p>
        <p className="smaller-title-text">Fontes: Planalto e IBGE </p>
        <p className="infos">
            <img alt="" src={thumbnail}/>
        </p>
    </div>
)


