import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Brasil  o segundo produtor mundial de biodiesel</h2>

        <p className="simple-text">Em 2017, a produo de etanol foi de 27 bilhes de litros e de mais de 4 bilhes de litros de biodiesel, destacando o Brasil como o segundo produtor mundial na categoria.</p>
        <p className="simple-text">Para continuar a incentivar a produo de biocombustveis e cumprir com o Acordo de Paris, o Governo do Brasil sancionou em 2017 a lei que cria o RenovaBio, um novo marco regulatrio para os biocombustveis e mais um estmulo para a inovao no Brasil.</p>
        <p className="eye-text">Com essa politica nacional,  possvel traar um plano para garantir o papel estratgico dos biocombustveis, principalmente quando se fala em segurana energtica e em reduo de emisses de gases causadores do efeito estufa.</p>
        <p className="smaller-title-text">Fonte: MME</p>
    </div>
)


