import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Aps quedas consecutivas, indstria automotiva cresce 17,2% em 2017</h2>
        <p className="simple-text">Aps quatro anos de queda, a produo de automveis, reboques e carrocerias no Brasil teve um aumento de 17,2% em 2017. Com melhora no ambiente econmico, a confiana das empresas tambm cresceu em relao  economia brasileira, possibilitando a retomada da produo.</p>
        <p className="eye-text">O setor automotivo  responsvel por 22% do PIB industrial brasileiro e emprega direta e indiretamente cerca de 1,6 milho de trabalhadores.</p>
        <p className="simple-text">Para 2018, o cenrio continua animador. Nos primeiros trs meses do ano, foram fabricados 699,7 mil veculos no Brasil, um aumento de 14,6% na comparao com o mesmo perodo de 2017. Segundo a Associao Nacional dos Fabricantes de Veculos Automotores (Anfavea), a previso  que a produo cresa 13,2% na comparao com o ano passado, atingindo a marca de 3,055 milhes de veculos montados.</p>
        <p className="smaller-title-text">Fonte: IBGE e Anfavea</p>
    </div>
)


