import React from 'react';
import HeaderContent from '../../components/HeaderContent';
import thumbnail from '../../assets/img/infos/[Banner]-2_anos_materia_19.jpg';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Brasil tem safra recorde de 238 milhes de toneladas de gros e aumenta exportaes em 2017</h2>

        <p className="simple-text">Em 2017, a safra recorde de 238 milhes de toneladas de gros, especialmente de milho e de soja, ajudou a derrubar a inflao a patamares inditos e contribuiu tambm para encerrar o perodo de recesso vivido no Pas. Foi a maior produo registrada na srie histrica do Instituto Brasileiro de Geografia e Estatstica (IBGE), iniciada em 1996</p>
        <p className="eye-text">Em 2018, a tendncia de grandes colheitas deve se repetir e atingir 229,5 milhes de toneladas, sendo a segunda maior da histria.</p>
        <p className="simple-text">Os nmeros do campo tambm refletem nos resultados positivos da balana comercial do Brasil. As exportaes do agronegcio representaram 44,1% do total, contribuindo para o saldo de US$ 67 bilhes na balana comercial.</p>
        <p className="smaller-title-text">Fontes: Conab,  IBGE e Mapa</p>
        <p className="infos">
            <img alt="" src={thumbnail}/>
        </p>
    </div>
)


