import React from 'react';
import HeaderContent from '../../components/HeaderContent';
import thumbnail from '../../assets/img/infos/[Info]-2_anos_materia-5.png';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Mais de 204 mil vagas com carteira assinada foram criadas em 2018</h2>

        <p className="simple-text">Depois de medidas do Governo do Brasil que ajudaram a organizar as contas pblicas e a derrubar a inflao e os juros, o mercado de trabalho volta a apresentar nmeros positivos em 2018. Nos primeiros trs meses do ano, mais de 204 mil postos formais foram efetivados e o cenrio de recuperao se consolida.</p>
        <p className="eye-text">Importante termmetro para a atividade econmica, a rea de servios obteve o principal resultado entre setores econmicos no ms de maro. </p>
        <p className="simple-text">Nesse caso, foram abertos 57.384 novos empregos. A Indstria de Transformao foi o segundo setor com melhores resultados (+10.450 postos), com um acrscimo de 0,14% sobre fevereiro.</p>
        <p className="simple-text">Levantamento da Confederao Nacional da Indstria (CNI) mostra que o brasileiro est mais satisfeito com a vida e mais confiante na manuteno do prprio emprego. Em comparao com junho de 2016, momento quando o medo do desemprego foi mais forte, o indicador caiu 6,04%. A pesquisa cria uma pontuao e, no caso do desemprego, quanto mais ela cair, melhor.</p>
        <p className="smaller-title-text">Fonte: Ministrio do Trabalho e CNI</p>
        <p className="infos">
            <img alt="" src={thumbnail}/>
        </p>
    </div>
)


