import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Cartrios de todo o Brasil podero emitir passaporte e carteiras de identidade</h2>
        <p className="simple-text">Obter a carteira de identidade e o passaporte fica mais fcil para os brasileiros em 2018. Com medida publicada pela Corregedoria Nacional de Justia, a emisso de tais documentos no est mais condicionada s secretarias de Segurana dos Estados ou  Polcia Federal. Agora, os cartrios podem auxiliar o Poder Executivo a expedir os documentos, ampliando a rede de atendimento.</p>
        <p className="simple-text">O intuito do governo  diminuir a burocracia e facilitar a vida do cidado. Para garantir a idoneidade do processo, os convnios locais passaro pelas corregedorias dos tribunais estaduais e os federais, pela Corregedoria Nacional. A Polcia Federal fica responsvel pelos acordos relativos  emisso do passaporte e continua sendo o rgo responsvel pela emisso do documento.</p>
        <p className="smaller-title-text">Fonte: Conselho Nacional de Justia</p>
    </div>
)


