import React from 'react';
import HeaderContent from '../../components/HeaderContent';
import thumbnail from '../../assets/img/infos/[Banner]-2_anos_materia_29.jpg';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Programa Mais Alfabetizao ter investimento de R$ 523 milhes nos prximos dois anos</h2>

        <p className="simple-text">Lanado em 2018 pelo Governo do Brasil para favorecer o desenvolvimento dos nveis de leitura, escrita e matemtica dos alunos matriculados nos dois primeiros anos do ensino fundamental, o Programa Mais Alfabetizao ter investimento de R$ 523 milhes em 2018 e 2019.</p>
        <p className="eye-text">Com a iniciativa, o governo ir atender e melhorar a performance de 3,6 milhes de alunos em 156 mil turmas das 49 mil escolas de todo o Pas j cadastradas. </p>
        <p className="simple-text">Professores do programa tero apoio tcnico de um assistente de alfabetizao em cada turma. Esse profissional passar cinco ou 10 horas semanais em sala, dando suporte ao professor alfabetizador.</p>
        <p className="simple-text">O Mais Alfabetizao faz parte da Poltica Nacional de Alfabetizao, lanada pelo MEC em 2017, e corrobora com a Lei de Diretrizes e Bases da Educao Nacional (LDB), que determina o desenvolvimento da capacidade de aprender, tendo como meios bsicos o pleno domnio da leitura, da escrita e do clculo.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Educao</p>
        <p className="infos">
            <img alt="" src={thumbnail}/>
        </p>
    </div>
)


