import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Discutido h mais de 20 anos, novo Ensino Mdio traz mais educao para os jovens </h2>

        <p className="simple-text">Depois de mais de 20 anos de debates, a reforma do ensino mdio foi aprovada pelo Congresso Nacional e sancionada pelo presidente da Repblica, Michel Temer, em fevereiro de 2017. As mudanas so mais um passo decisivo na modernizao do Pas ao estabelecer novas regras, mais adequadas  sociedade brasileira atual.</p>
        <p className="eye-text">Com as mudanas, o modelo brasileiro para o ensino mdio se aproxima daqueles adotados nos pases que so referncia de educao no mundo.</p>
        <p className="simple-text">O novo sistema de educao dos jovens faz da educao uma prioridade e cria condies para transformar a vida de milhes de brasileiros. Entre as modificaes est a possibilidade de o currculo ficar mais flexvel, permitindo que o aluno escolha a rea de seu maior interesse para aprofundamento de estudos ou uma rea de ensino tcnico profissional.</p>
        <p className="simple-text">Destaque ainda para ampliao do ensino integral: foram 500 mil novas vagas de tempo integral em 2017. Todas voltadas para o jovem do ensino mdio, beneficiando todos os estados e o Distrito Federal.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Educao</p>
    </div>
)


