import React from 'react';
import HeaderContent from '../../components/HeaderContent';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Modernizao trabalhista: novos contratos esquentam o mercado de trabalho</h2>

        <p className="simple-text">Com a entrada em vigor da modernizao trabalhista em novembro de 2017, j  possvel ver resultados concretos com a assinatura de novos tipos de contratos de emprego. </p>
        <p className="eye-text">Em maro, mais de 3 mil vagas foram abertas no modelo intermitente  aqueles empregos no constantes, que pode haver perodos de muito trabalho e perodos sem muita atividade.</p>
        <p className="simple-text">Na modalidade de contrato parcial, foram registradas a criao de mais 3 mil novas oportunidades de emprego, tambm no ms de maro. Esses contratos anteriormente no permitiam que o trabalhador fizesse hora extra, o que foi alterado na modernizao trabalhista. Por fim, o home office comeou a ser implantado com mais fora no mercado e contabiliza at fevereiro 119 empregos formais. </p>
        <p className="smaller-title-text">Fonte: Ministrio do Trabalho</p>
    </div>
)


