import React from 'react';
import HeaderContent from '../../components/HeaderContent';
import thumbnail from '../../assets/img/infos/[banner]-2_anos_materia-46-01.jpg';
export default () => (
    <div>
        <HeaderContent></HeaderContent>
        <h2 className="materia-title">Dois anos de gesto: mais de R$ 10 bilhes de economia para os brasileiros</h2>

        <p className="eye-text">Com aes inovadoras para diminuir a burocracia, gesto eficiente e combate  corrupo, o Governo do Brasil gerou economias de mais de R$ 10 bilhes, podendo reinvestir em servios e programas para toda a populao.</p>
        <p className="simple-text">Somente na rea da sade, entre maio de 2016 e dezembro de 2017, foram R$ 5 bilhes economizados com aes administrativas, negociao e reviso de contratos. O valor foi reinvestido completamente no Sistema nico de Sade (SUS), servindo para habilitar novos servios, custear unidades de Pronto Atendimento (UPAs) e o Servio de Atendimento Mvel de Urgncia (SAMU), assim como para credenciar novas equipes na ateno bsica.</p>
        <h3>Pente-fino</h3>
        <p className="simple-text">As auditorias e fiscalizaes do Ministrio da Transparncia tambm se destacam. A deteco dos casos de corrupo, o cancelamento de contratos e licitaes desnecessrios e a reduo de custos representaram para os cofres pblicos mais de R$ 4,6 bilhes de economia efetiva em 2017. </p>
        <p className="simple-text">J com as operaes de averiguao de fraudes no seguro desemprego, o Ministrio do Trabalho pde evitar, entre dezembro de 2016 e fevereiro de 2018, o gasto irregular de R$ 750 milhes. </p>
        <p className="simple-text">Os programas sociais tambm passaram por um pente-fino. Com gesto aperfeioada, o benefcio est indo para quem realmente precisa e pagamentos indevidos foram suspensos. Somente no programa Bolsa Atleta, foram salvos R$ 13 milhes.</p>
        <p className="smaller-title-text">Fonte: Ministrio da Transparncia, Ministrio do Trabalho, Ministrio da Sade</p>
        <p className="infos">
            <img alt="" src={thumbnail}/>
        </p>
    </div>
)


